<template>
  <div id="app" class="container">
    <div class="jumbotron jumbotron-fluid">
      <div class="container">
        <div class="text-center">
          <img src="./assets/logo.png" style="max-height: 50px">
          <p class="lead">Consumo de Servicio SOAP en VUE JS</p>
        </div>
      </div>
    </div>
    <div class="container">
      <div v-if="paso===0">
        <div class="progress">
          <div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" aria-valuenow="100" aria-valuemin="0" aria-valuemax="100" style="width: 100%"></div>
        </div>
        <div class="text-center">
          Procesando
        </div>
      </div>
      <div v-if="paso===1">
        <div>
          <div class="form-group">
            <label>Dirección del WS</label>
            <input type="url" class="form-control" placeholder="http://localhost:2962/WebPublicaciones.asmx" v-model="url">
          </div>
          <div class="form-group">
            <label>Ingrese Solicitud Soap</label>
            <textarea class="form-control" rows="8" v-model="cabecera" :placeholder="cabeceraE"></textarea>
          </div>
        </div>
        <button type="button" class="btn btn-success" v-on:click="consulta">Consultar</button>
      </div>
      <div v-if="paso===2">
        <div class="alert alert-success" role="alert">
          Resultado de la consulta SOAP
        </div>
        <div class="list-group">
          <a href="#" class="list-group-item list-group-item-action flex-column align-items-start" v-for="(item,index) in lista" :key="index">
            <div class="d-flex w-100 justify-content-between">
              <h5 class="mb-1">{{item.titulo_pu[0]}}</h5>
              <small>{{item.autores_pu[0]}}</small>
            </div>
            <p class="mb-1">{{item.cita_pu[0]}}</p>
            <div class="form-inline">
              <a class="btn btn-link btn-sm" target="_blank" v-for="(boton,index2) in item.enlaces[0].enlaces" :key="index2" :href="boton.link_en[0]">{{boton.tipo[0].titulo_te[0]}}</a>
            </div>
          </a>
        </div>
      </div>
      <div v-if="paso===3">
        <div class="alert alert-danger" role="alert">
          <h4 class="alert-heading">Ha ocurrido un error</h4>
          <p>Prueba que la URL y la Cabecera de peticion este correcta y que el WEB SERVICE tenga activado la cabecera CORS</p>
          <hr>
          <p class="mb-0">Intenta de nuevo</p>
          <button type="button" class="btn btn-dark" v-on:click="paso=1">Regresar</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  var parseString = require('xml2js').parseString;
export default {
  name: 'App',
  data:()=>({
    url:"http://puceing.edu.ec:15005/acanizares/Practica%207/WebPublicaciones.asmx",
    cabecera:"<?xml version=\"1.0\" encoding=\"utf-8\"?>\n" +
      "<soap:Envelope xmlns:xsi=\"http://www.w3.org/2001/XMLSchema-instance\" xmlns:xsd=\"http://www.w3.org/2001/XMLSchema\" xmlns:soap=\"http://schemas.xmlsoap.org/soap/envelope/\">\n" +
      "  <soap:Body>\n" +
      "    <Publicaciones xmlns=\"http://tempuri.org/\" />\n" +
      "  </soap:Body>\n" +
      "</soap:Envelope>",
    cabeceraE:"<?xml version=\"1.0\" encoding=\"utf-8\"?>\n" +
      "<soap:Envelope xmlns:xsi=\"http://www.w3.org/2001/XMLSchema-instance\" xmlns:xsd=\"http://www.w3.org/2001/XMLSchema\" xmlns:soap=\"http://schemas.xmlsoap.org/soap/envelope/\">\n" +
      "  <soap:Body>\n" +
      "    <Publicaciones xmlns=\"http://tempuri.org/\" />\n" +
      "  </soap:Body>\n" +
      "</soap:Envelope>",
    lista:null,
    cargando:false,
    paso:1,
  }),
  methods:{
    consulta:function(){
      this.paso=0;
      let aux1;
      let aux2;
      axios.defaults.headers.post['Content-Type'] = 'text/xml';
      axios.post(this.url,this.cabecera)
        .then(res=>{
          let self = this;
          parseString(res.data, function (err, result) {
            aux1 = result["soap:Envelope"]["soap:Body"];
            aux2 = aux1[0]["PublicacionesResponse"][0]["PublicacionesResult"];
            self.lista=aux2[0]["publicaciones"];
          });
          this.paso=2;
        }).catch(err=>{
          console.log(err)
        this.paso=3;
        });
    }
  },
  mounted() {
    this.consulta();
  }

}
</script>

<style>
  @import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
  .vue{
    background-color: #41b883;
  }
</style>



